export const TINYSYNQ_PREFIX = 'synq';
export const TINYSYNQ_BATCH_SIZE = 20;
export const TINYSYNQ_NANOID_SIZE = 16;

export const SYNQ_INSERT = 'INSERT';
export const SYNQ_UPDATE = 'UPDATE';
export const SYNQ_DELETE = 'DELETE';

export const TINYSYNQ_ISO8601_REGEX = /^\d{4}(-\d{2}){2}T(\d{2}:){2}\d{2}\.\d{1,3}Z$/
export const TINYSYNQ_SAFE_ISO8601_REGEX = /^\d{4}(-\d{2}){2}\s(\d{2}:){2}\d{2}(\.\d{1,3})?$/